.notes-container > h1 {
  text-align: center;
  padding: 40px 0 24px;
  font-size: 36px;
  font-weight: 500;
  cursor: default;
}
.notes-container > h1:hover {
  animation-name: notes-title-horizontal;
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: 1;
}
.notes-container .tag {
  margin-bottom: 36px;
}
.notes-container .tag > nav {
  border-radius: 12px;
  font-size: 14px;
  font-weight: 600;
  margin: 0 6px;
  padding: 4px 12px;
  transition: opacity 0.3s;
  cursor: pointer;
}
.notes-container .tag > nav:hover {
  opacity: 0.8;
}
.notes-container .tag > nav:nth-child(1) {
  background: rgba(235, 47, 150, 0.2);
  color: #eb2f96;
}
.notes-container .tag > nav:nth-child(2) {
  background: rgba(250, 173, 20, 0.2);
  color: #faad14;
}
.notes-container .tag > nav:nth-child(3) {
  background: rgba(114, 46, 209, 0.2);
  color: #722ed1;
}
.notes-container .tag > nav:nth-child(4) {
  background: rgba(82, 196, 26, 0.2);
  color: #52c41a;
}
.notes-container .tag > nav:nth-child(5) {
  background: rgba(24, 144, 255, 0.2);
  color: #1890ff;
}
.notes-container .tag > nav:nth-child(6) {
  background: rgba(19, 194, 194, 0.2);
  color: #13c2c2;
}
.notes-container .content .multi-card {
  display: flex;
  justify-content: space-between;
}
.notes-container .content .comp-navigation-container {
  margin-bottom: 20px;
}
@keyframes notes-title-horizontal {
  16.65% {
    transform: translateX(8px);
  }
  33.3% {
    transform: translateX(-6px);
  }
  49.95% {
    transform: translateX(4px);
  }
  66.6% {
    transform: translateX(-2px);
  }
  83.25% {
    transform: translateX(1px);
  }
  100% {
    transform: translateX(0);
  }
}
